@custom-media --xs (min-width: 480px);
@custom-media --xs-down (max-width: 479px);
@custom-media --sm (min-width: 640px);
@custom-media --sm-down (max-width: 639px);
@custom-media --md (min-width: 768px);
@custom-media --md-down (max-width: 767px);
@custom-media --lg (min-width: 1024px);
@custom-media --lg-down (max-width: 1023px);
@custom-media --xl (min-width: 1280px);
@custom-media --xl-down (max-width: 1279px);
@custom-media --2-xl (min-width: 1536px);
@custom-media --2-xl-down (max-width: 1535px);
.gform_wrapper {
  margin-top: 0;
}
.gform_wrapper .gform_fields {
  display: grid;
  gap: var(--wp--custom--spacing--block-gap);
}
@media (--md) {
  .gform_wrapper .gform_fields {
    grid-template-columns: repeat(12, 1fr);
  }
}
.gform_wrapper .ginput_complex {
  display: flex;
  flex-wrap: wrap;
  gap: var(--wp--custom--spacing--block-gap);
}
.gform_wrapper .ginput_complex > * {
  flex: 1;
}
.gform_wrapper .ginput_complex .ginput_full {
  flex: 0 0 100%;
}
.gform_wrapper .gfield {
  grid-column: 1/-1;
}
@media (--md) {
  .gform_wrapper .gfield--width-quarter {
    grid-column: span 3;
  }
  .gform_wrapper .gfield--width-third {
    grid-column: span 4;
  }
  .gform_wrapper .gfield--width-five-twelfths {
    grid-column: span 5;
  }
  .gform_wrapper .gfield--width-half {
    grid-column: span 6;
  }
  .gform_wrapper .gfield--width-seven-twelfths {
    grid-column: span 7;
  }
  .gform_wrapper .gfield--width-two-thirds {
    grid-column: span 8;
  }
  .gform_wrapper .gfield--width-three-quarter {
    grid-column: span 9;
  }
}
.gform_wrapper .hidden_label > .gfield_label {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  width: 1px;
  word-wrap: normal;
  z-index: 100000;
}
.gform_wrapper .gform_required_legend {
  display: none;
}
.gform_wrapper .gfield_required {
  color: var(--wp--custom--color--text--error);
}
.gform_wrapper .gfield_label {
  margin-bottom: var(--wp--preset--spacing--xs);
}
.gform_wrapper .gfield_label > .gfield_required {
  margin-left: var(--wp--preset--spacing--2-xs);
}
.gform_wrapper .ginput_container > :where(input, select):where(:not([type=radio], [type=checkbox])) {
  width: 100%;
}
.gform_wrapper .gfield--type-captcha label {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  width: 1px;
  word-wrap: normal;
  z-index: 100000;
}
.gform_wrapper .ginput_container_radio label,
.gform_wrapper .ginput_container_checkbox label,
.gform_wrapper .ginput_container_consent label {
  display: inline-block;
  margin: 0;
  vertical-align: middle;
}
.gform_wrapper .ginput_container_consent {
  display: inline-grid;
  gap: var(--wp--preset--spacing--xs);
  grid-template-columns: minmax(16px, max-content) auto;
}
.gform_wrapper .ginput_container_consent input {
  position: relative;
  top: 4px;
}
.gform_wrapper .gform_confirmation_message {
  text-align: center;
}
.gform_wrapper .gform_button {
  align-items: center;
  border: unset;
  border-radius: unset;
  box-shadow: unset;
  cursor: pointer;
  display: inline-flex;
  gap: var(--wp--preset--spacing--2-xs);
  justify-content: center;
  padding: 16px 32px;
  text-align: center;
  transition: all var(--wp--custom--transition-duration--quick);
  font-weight: 500;
}
.gform_wrapper .gform_button::before {
  content: "";
  height: 24px;
  width: 24px;
}
.gform_wrapper .gform_validation_errors {
  margin-bottom: var(--wp--preset--spacing--lg);
}
.gform_wrapper .gform_footer {
  margin-top: var(--wp--preset--spacing--xl);
}
.gform_wrapper .gform_submission_error {
  color: var(--wp--custom--color--text--error);
  font-size: var(--wp--preset--font-size--md);
  font-weight: 400;
  margin-top: 0;
  text-transform: none;
}
.gform_wrapper .gform_submission_error:focus {
  outline: 0;
}
.gform_wrapper .gfield_error input,
.gform_wrapper .gfield_error select,
.gform_wrapper .gfield_error textarea {
  border-color: var(--wp--custom--color--border--error);
}
.gform_wrapper .validation_message {
  color: var(--wp--custom--color--text--error);
  margin-top: var(--wp--preset--spacing--sm);
}
.gform_wrapper .gform_title {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  width: 1px;
  word-wrap: normal;
  z-index: 100000;
}
.gform_wrapper .gform_hidden {
  display: none;
}
.gform_wrapper .gfield_visibility_hidden {
  left: -9999px;
  position: absolute;
  visibility: hidden;
}
.gform_wrapper .gform_validation_container {
  display: none !important;
  left: -9999px;
  position: absolute !important;
}

.gform_ajax_spinner {
  display: none;
}

.gform_button__loading {
  animation: 1000ms rotate linear infinite;
  border: 3px solid;
  border-color: currentcolor rgba(0, 0, 0, 0) currentcolor rgba(0, 0, 0, 0);
  border-radius: 50%;
  box-sizing: border-box;
  display: inline-block;
  height: 24px;
  position: relative;
  scale: 0.6;
  visibility: hidden;
  width: 24px;
}
.is-loading .gform_button__loading {
  visibility: visible;
}
.gform_button__loading::before, .gform_button__loading::after {
  border: 5px solid transparent;
  border-bottom-color: currentcolor;
  content: "";
  left: 0;
  position: absolute;
  top: 0;
  transform: translate(-5px, 9.5px) rotate(-35deg);
}
.gform_button__loading::after {
  border-color: currentcolor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  transform: translate(16px, 1.5px) rotate(-35deg);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}